import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";

import { ToyList } from "../cmps/toy-list";
import { ToyFilter } from "../cmps/toy-filter";
import { toyService } from "../services/toy.service";

import { showSuccessMsg, showErrorMsg } from '../services/event-bus.service.js'
import { loadToys, removeToy } from "../store/toy.action.js";
import { ADD_TO_CART } from "../store/toy.reducer";
import { useNavigate } from "react-router-dom";


export function ToyIndex() {

    const toys = useSelector((storeState) => storeState.toyModule.toys)
    const isLoading = useSelector((storeState) => storeState.toyModule.isLoading)
    const navigate = useNavigate()
    // console.log(toys);

    const dispatch = useDispatch()

    useEffect(() => {
        onLoadToys()
    }, [])

    function onLoadToys(filterBy) {
        loadToys(filterBy)
            .then(() => {
                showSuccessMsg('Toys loaded')
            })
            .catch(err => {
                showErrorMsg('Cannot load toys', err)
            })
    }

    function onAddToCart(toy) {
        dispatch({ type: ADD_TO_CART, toy })
        showSuccessMsg('Added to Cart')
    }

    function onRemoveToy(toyId) {
        removeToy(toyId)
            .then(() => {
                showSuccessMsg('Toy removed')
            })
            .catch(err => {
                showErrorMsg('Cannot remove toy')
            })
    }

    function onSetFilter(filterBy) {
        onLoadToys(filterBy)
    }

    return (
        <section>

            <h3>ToyIndex</h3>
            <ToyFilter onSetFilter={onSetFilter} />
            <button onClick={() => toyService.createDefaultToy()}>create default toy</button>
            <button onClick={() => navigate('/toy/edit')}>create your toy</button>

            {isLoading && <p>Loading...</p>}

            <ToyList
                toys={toys}
                onAddToCart={onAddToCart}
                onRemoveToy={onRemoveToy} />

        </section>

    )
}