import { useEffect, useState } from "react"
import { Link, useNavigate, useParams } from "react-router-dom"
import { MultiSelect } from "../cmps/multi-select"
import { showErrorMsg, showSuccessMsg } from "../services/event-bus.service"
import { toyService } from "../services/toy.service"


export function ToyEdit() {
    const [toyToEdit, setToyToEdit] = useState(toyService.getEmptyToy())
    const navigate = useNavigate()
    const { toyId } = useParams()

    // console.log(toyToEdit);

    useEffect(() => {
        if (toyId) loadToy()

    }, [])

    function loadToy() {
        toyService.getById(toyId)
            .then(setToyToEdit)
            .catch((err) => {
                console.log('Had issues in toy edit', err)
                showErrorMsg('Cannot load toy')
                navigate('/toy')
            })
    }

    function handleChange({ target }) {
        let { value, type, name: field } = target
        value = type === 'number' ? +value : value
        setToyToEdit((prevToy) => ({ ...prevToy, [field]: value }))
    }


    function handleSelectChange(ev) {
        // let { labels } = toyToEdit
        // let labelsToEdit = labels

        let labelsToEdit = ev.map(l => l.value)
        console.log(labelsToEdit);
        setToyToEdit((prevToy) => ({ ...prevToy, labels: labelsToEdit }))
    }

    function handleInStockCheckboxChange({ target }) {
        console.log(target.checked);
        setToyToEdit((prevToy) => ({ ...prevToy, inStock: target.checked }))
    }


    function onSaveToy(ev) {
        ev.preventDefault()
        toyService.save(toyToEdit)
            .then((toy) => {

                console.log('toy saved', toy);
                showSuccessMsg('toy saved!')
                navigate('/toy')
            }
            )
            .catch((err) => {
                console.log('err', err)
                showErrorMsg('Cannot save toy')
            })
    }

    return (
        <section>
            <h2>{toyToEdit._id ? 'Edit this toy' : 'Add a new toy'}</h2>

            <form onSubmit={onSaveToy}>
                <label htmlFor="name">name : </label>
                <input type="text"
                    name="name"
                    id="name"
                    placeholder="Enter name..."
                    value={toyToEdit.name}
                    onChange={handleChange}
                />
                <label htmlFor="price">Price : </label>
                <input type="number"
                    name="price"
                    id="price"
                    placeholder="Enter price"
                    value={toyToEdit.price}
                    onChange={handleChange}
                />
                <label htmlFor="price">in stock? </label>
                <input type="checkbox" name="inStock" id="inStock"
                    checked={toyToEdit.inStock}
                    onChange={handleInStockCheckboxChange} />
                <label htmlFor="inStock">In stock</label>
                <br />

                <MultiSelect handleSelectChange={handleSelectChange} />

                <div>
                    <button>{toyToEdit._id ? 'Save' : 'Add'}</button>
                    <Link to="/toy">Cancel</Link>
                </div>
            </form>
        </section>
    )
}