
// import { storageService } from './async-storage.service.js'
import { httpService } from './http.service.js'
import { userService } from './user.service.js'

// const STORAGE_KEY = 'toyDB'
const BASE_URL = 'toy/'

export const toyService = {
    query,
    getById,
    save,
    remove,
    getEmptyToy,
    getDefaultFilter,
    createDefaultToy
}


function getDefaultFilter() {
    return { txt: '', maxPrice: 0, labels: [] }
}

function getEmptyToy(name = '', price = 0, labels = [], imgUrl = '') {
    return {
        name,
        price,
        labels,
        inStock: true,
        imgUrl
    }
}

function query(filterBy = getDefaultFilter()) {

    const queryPatams = `?name=${filterBy.txt}&maxPrice=${filterBy.maxPrice}&labels=${filterBy.labels}`
    return httpService.get(BASE_URL + queryPatams)
}

function getById(toyId) {
    return httpService.get(BASE_URL + toyId)
    // return storageService.get(STORAGE_KEY, toyId)
}

function remove(toyId) {
    return httpService.delete(BASE_URL + toyId)
    // return storageService.remove(STORAGE_KEY, toyId)
}

function save(toy) {
    if (toy._id) {
        return httpService.put(BASE_URL, toy)
    } else {
        // when switching to backend - remove the next line
        toy.createdAt = new Date().getTime()
        toy.owner = userService.getLoggedinUser()._id
        return httpService.post(BASE_URL, toy)
    }
}

function createDefaultToy() {
    let toy = getEmptyToy('talking doll', 10, ["Doll", "Battery Powered", "Baby"], '../assets/img/baby_doll.jpg')

    save(toy)
}