
import React, { useState } from "react";
import GoogleMapReact from 'google-map-react';

const AnyReactComponent = ({ text }) => <div>{text}</div>;

export function BranchesMap() {

    let branches = [
        { lat: 32.087, lng: 34.781, txt: 'tel-aviv' },
        { lat: 32.0694, lng: 34.8083, txt: 'givatayim' },
        { lat: 32.013, lng: 34.786, txt: 'holon' },
    ]

    const [coordinates, serCoordinates] = useState({ lat: 32.085, lng: 34.781 })
    const [currentBranch, setCurrentBranch] = useState(branches[0])
    const zoom = 11


    const handleClick = (x) => {
        console.log(x);
    }
    const handleChildClick = (x) => {
        console.log(x);
        serCoordinates(branches[x])
        setCurrentBranch(branches[x])
    }

    const onChildMouseEnter = (x) => {
        console.log(x);
    }

    return (
        // Important! Always set the container height explicitly
        <div style={{ height: '400px', width: '400px' }}>
            <GoogleMapReact
                onChildClick={handleChildClick}
                onClick={handleClick}
                bootstrapURLKeys={{ key: "AIzaSyDi7GMy6lxgjHiG_Uiom82iOxAwhazf7Ro" }}
                // defaultCenter={coordinates}
                center={coordinates}
                defaultZoom={zoom}

            >

                <AnyReactComponent
                    lat={branches[0].lat}
                    lng={branches[0].lng}
                    text="🚩"
                />

                <AnyReactComponent
                    lat={branches[1].lat}
                    lng={branches[1].lng}
                    text="🚩"
                />

                <AnyReactComponent
                    lat={branches[2].lat}
                    lng={branches[2].lng}
                    text="🚩"
                />


                <AnyReactComponent
                    lat={currentBranch.lat}
                    lng={currentBranch.lng}
                    text={currentBranch.txt}
                />


            </GoogleMapReact>
        </div>
    );
}
