import React from 'react';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut } from 'react-chartjs-2';

ChartJS.register(ArcElement, Tooltip, Legend);


export function MyPriceChart({ toys }) {

    const priceCount = toys.reduce((acc, toy) => {
        if (!acc[toy.price]) acc[toy.price] = 0
        acc[toy.price]++
        return acc
    }, {})

    const priceDatas = []
    const priceValues = []
    for (const price in priceCount) {
        priceDatas.push(price)
        priceValues.push(priceCount[price])
    }

    const data = {
        labels: priceDatas,
        datasets: [
            {
                label: '# of Votes',
                data: priceValues,
                backgroundColor: [
                    'rgba(255, 99, 132, 0.2)',
                    'rgba(54, 162, 235, 0.2)',
                    'rgba(255, 206, 86, 0.2)',
                    'rgba(75, 192, 192, 0.2)',
                    'rgba(153, 102, 255, 0.2)',
                    'rgba(255, 159, 64, 0.2)',
                    'rgba(64, 255, 223, 0.2)'
                ],
                borderColor: [
                    'rgba(255, 99, 132, 1)',
                    'rgba(54, 162, 235, 1)',
                    'rgba(255, 206, 86, 1)',
                    'rgba(75, 192, 192, 1)',
                    'rgba(153, 102, 255, 1)',
                    'rgba(255, 159, 64, 1)',
                    'rgba(64, 255, 223, 1)'
                ],
                borderWidth: 1,
            },
        ],
    };
    return <Doughnut data={data} />;
}
